<script>
import messageVue from "./message.vue";
import socket from "socket.io-client";
import { eventBus } from "../main.js";
import { url } from "../main.js";

import axios from "axios";
export default {
  mounted() {
    //===== Mobile Menu
    const navigation = document.getElementById("nav");
    navigation.classList.add("sticky");

    window.addEventListener("scroll", (event) => {
      var scroll = window.pageYOffset;
      if (scroll < 10) {
        navigation.classList.add("sticky");
      } else {
        if (navigation.classList.contains("sticky")) {
          navigation.classList.remove("sticky");
        }
      }
    });
  },
  created() {
    this.io.on("live", () => {
      this.live = !this.live;
    });

    this.io.on("finish", () => {
      this.live = false;
    });
  },
  data() {
    return {
      io: socket(axios.defaults.baseURL),
      live: false,
      links: [
        // {page: '', func: () => this.$router.push('/exam')},
        // {page: ' ', func: () => this.$router.push('/lessons')},
        // {page: ' ', func: () => this.$router.push('/liveVideo')},
      ],
    };
  },
  computed: {
    message() {
      return this.$store.getters.message;
    },
    username() {
      return this.$store.getters.username;
    },
    token() {
      return this.$store.getters.token;
    },
    role() {
      return this.$store.getters.role;
    },
    adminData() {
      return this.$store.getters.adminData;
    },
    showHeader() {
      return this.$store.getters.showHeader;
    },
  },
  components: {
    appMessage: messageVue,
  },
  watch: {
    $route() {
      const nav = document.getElementById("nav-btn");
      // console.log([...nav.classList].includes("collapsed"));
      if ([...nav.classList].includes("collapsed")) return;
      nav.click();
    },
  },
  methods: {
    openRegisterModal() {
      this.$store.dispatch("openModal");
    },
    logout() {
      this.$store.dispatch("logout");
    },
    toggleDrawer() {
      this.$store.dispatch("toggleDrawer");
    },
    toggleDrawer2() {
      this.$store.dispatch("orderModalOpen");
    },
    back() {
      this.$router.back();
    },
    home() {
      this.$router.push("/");
    },
    updateUserInfo() {
      this.$router.push("/update");
    },
  },
};
</script>

