import axios from 'axios';

const state = {
    lessons: [],
}

const mutations = {}

const actions = {
    getLessons({rootState}) {
        if(rootState.generalStore.role == 0) return;
        axios.get(`/getAllLessons`).then(res => {
            if(res.data.lessons.length > 0) state.lessons = res.data.lessons;
        })
    },
    adjust({rootState}) {
        console.log("adjust")
        console.log(rootState.generalStore.adminData)
        if(!rootState.generalStore.adminData.adjust) return;
        // axios.get("/adjustLessons").then(res => {
        //     if(res.data.err) return;
        //     console.log("Done!!");
        // });
        ////////////////////////////////
        axios.get("/adjustExams").then(res => {
            if(res.data.err) return;
            console.log("Done!!");
        });
    }
}

const getters = {
    lessons() {
        return state.lessons;
    }
}

export default {state, mutations, actions, getters};