<template>
  <div style="position: absolute; bottom: 0">
    <!-- <section>Footer Example 4</section> -->
    <footer class="footer-distributed">
      <div class="footer-left">
        <h3 style="cursor: pointer" @click="gotoLink('dev-page')">
          Far<span> Web</span>
        </h3>

        <p class="footer-links">
          <a @click="$router.push('/fullcourse')" class="link-1">lessons</a>

          <a @click="$router.push('/register/home/signin')">register</a>

          <a @click="$router.push('/')">Home</a>
        </p>

        <p style="color: #fff" class="footer-company-name">Farweb  2022</p>
      </div>

      <div class="footer-center">
        <div>
          <i class="fa fa-map-marker"></i>
          <p>
            <span>{{ adminData.address }}</span>
          </p>
        </div>

        <div>
          <i class="fa fa-phone"></i>
          <p>{{ adminData.phone }}</p>
        </div>

        <div>
          <i class="fa fa-envelope"></i>
          <p>
            <a @click="gotoLink('email')">{{ adminData.email }}</a>
          </p>
        </div>
      </div>

      <div class="footer-right">
        <p class="footer-company-about">
          <span>About the website</span>
          dedicated in online and self learning, academic and well trusted standard
          content, readable and visual content with all kinds of tests, quizes and tasks.
        </p>

        <div class="footer-icons">
          <a @click="gotoLink('facebook')"><i class="fa fa-facebook"></i></a>
          <a @click="gotoLink('telegram')"><i class="fa fa-telegram"></i></a>
          <a @click="gotoLink('whatsapp')"><i class="fa fa-whatsapp"></i></a>
          <a @click="gotoLink('email')"><i class="fa fa-envelope"></i></a>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import { Capacitor } from "@capacitor/core";
export default {
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
  },
  methods: {
    gotoLink(link) {
      if (link === "whatsapp") {
        window.open(
          "https://api.whatsapp.com/send?phone=2" + this.adminData.whatsapp,
          "_blank"
        );
      }

      if (link === "email") {
        window.open(
          "mailto:" +
            this.adminData.email +
            "?subject=Subject&body=message%20goes%20here",
          "_blank"
        );
      }

      if (link === "telegram") {
        window.open(this.adminData.telegram);
      }

      if (link === "facebook") {
        if (this.adminData.facebook.includes("messenger"))
          return window.open(this.adminData.facebook, "_blank");
        if (!navigator.platform.includes("Win32") || Capacitor.isNative) {
          window.open("fb://facewebmodal/f?href=" + this.adminData.facebook);
        } else {
          window.open(this.adminData.facebook, "_blank");
        }
      }

      if (link === "dev-page") {
        const page = "https://www.facebook.com/FarwebEgy";
        if (page.includes("messenger")) return window.open(page, "_blank");
        if (!navigator.platform.includes("Win32") || Capacitor.isNative) {
          window.open("fb://facewebmodal/f?href=" + page);
        } else {
          window.open(page, "_blank");
        }
      }
    },
  },
};
</script>

