<template>
  <div id="_all">
    <div v-if="!ready" style="padding-top: 50px" class="center">
      <div class="spinner-border text-info" role="status">
        <span class="sr-only">Loading...</span>
      </div>
    </div>
    <div v-if="ready">
      <!-- <div
        class="overlay-image"
        :style="{
          background:
            'url(\'' +
            $store.getters.adminData.image1.replace('http', 'https') +
            '\') fixed center center no-repeat',
          backgroundSize: '100% 100%',
        }"
      ></div> -->
      <!-- <div class="overlay-image">
        <img
          class="img-fluid"
          :src="$store.getters.adminData.image1.replace('http://', 'https://') || ''"
        />
      </div> -->
      <!----------------CHAT--------------->
      <div>
        <audio
          ref="audio"
          id="audio"
          controls="false"
          style="display: none"
          src="./assets/notification.mp3"
        ></audio>
        <!------------------------------------>
        <div
          v-if="token && !chatPage && $store.getters.adminData.chat"
          @click="gotoChat"
          class="btn-chat"
          id="livechat-compact-container"
          style="visibility: visible; opacity: 1"
        >
          <!---------------->
          <a @click="$router.push('/chat')" class="float" target="_blank">
            <i class="fa fa-comment my-float">{{ msgNumber > 0 ? msgNumber : "" }}</i>
          </a>
        </div>
        <!----------------CHAT-END--------------->
        <div id="all">
          <v-app id="inspire">
            <router-view name="header" />
            <transition name="slide" mode="out-in">
              <div class="page">
                <router-view></router-view>
              </div>
            </transition>
            <br /><br /><br /><br /><br />
            <app-footer />
          </v-app>

          <app-modal />
          <app-message />
          <div v-if="live" class="live-notification">
            <div class="box">
              <router-link to="/live-video" class="navbar-brand live"> LIVE </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import AppMessage from "./components/message.vue";
import AppFooter from "./components/footer.vue";
// import socket from "socket.io-client";
import LoadingPage from "./components/loading_page.vue";
// import { getAlignStyle } from "./functions";
//////////////////CHAT////////////////////
import { url, socket, eventBus } from "./main.js";

//////////////////CHAT-END////////////////////

export default {
  async created() {
    await this.initPage(this.$route);
    //////////////////////////////////////////
    await this.$store.dispatch("initApp");
    if (this.role == 0) {
      axios.get("/addVisitor").then(() => {});
    }

    //////////////////CHAT////////////////////
    socket().on("reconnect", () => {
      if (this.role == 0) socket().emit("joinRoom", this.$store.getters.userId);
      if (this.role == 1) socket().emit("joinAdminRoom");
      if (this.token && this.stage)
        socket().emit("register_as_user", {
          userId: this.$store.getters.userId,
          stage: this.stage,
        });
    });
    if (this.role == 0) socket().emit("joinRoom", this.$store.getters.userId);
    if (this.role == 1) socket().emit("joinAdminRoom");
    ////////////////////////////////
    if (this.$store.getters.adminData.renewHost) {
      this.$router.replace("/renewHost");
      this.chatPage = true;
    }

    socket().on("receiveMessage", (mess) => {
      if (mess.senderId != this.$store.getters.userId) {
        const audio = document.getElementById("audio");
        audio.play();
        if (this.chatPage && this.role == 0) return;
        const msgNumber = this.$store.getters.msgNumber;
        const inc = +msgNumber + 1;
        this.$store.dispatch("changeMsgNumber", inc);
      }
    });
    socket().on("live", ({ current_room, sec }) => {
      // if (this.stage != current_room) return this.$router.replace("/");
      this.live = !this.live;
      clearTimeout(this.liveDebounce);
      this.liveDebounce = setTimeout(() => {
        this.live = false;
      }, 2000);
    });
  },
  mounted() {},
  data() {
    return {
      // io: socket(axios.defaults.baseURL),
      live: false,
      chatPage: false,
      routerKey: "293jdfd9834",
      liveDebounce: null,
    };
  },
  computed: {
    ready() {
      return this.$store.getters.ready;
    },
    token() {
      return this.$store.getters.token;
    },
    role() {
      return this.$store.getters.role;
    },
    stage() {
      return this.$store.getters.stage;
    },
    //////////////////CHAT////////////////////
    msgNumber() {
      return this.$store.getters.msgNumber;
    },
    routerkey() {
      return this.$store.getters.routerKey;
    },
    //////////////////CHAT-END////////////////////
  },
  watch: {
    //////////////////CHAT////////////////////
    $route(to) {
      this.initPage(to);
      if (this.stage && this.token) {
        socket().emit("register_as_user", {
          userId: this.$store.getters.userId,
          stage: this.stage,
        });
      }
      document.getElementById("_all").scrollIntoView({ behavior: "smooth" });
    },
    //////////////////CHAT-END////////////////////
  },
  name: "app",
  components: {
    AppFooter,
    LoadingPage,
    AppMessage,
  },
  methods: {
    //////////////////CHAT////////////////////
    gotoChat() {
      if (this.role == 0) this.$router.push("/chat");
      else if (this.role == 1) this.$router.push("/dashboard/chat");
    },
    initPage(to) {
      // this.chatPage = true;
      this.$store.dispatch("generateRouterKey");
      if (this.$store.getters.adminData.renewHost) {
        this.chatPage = true;
        this.$router.replace("/renewHost");
        return;
      } else if (to.path.includes("chat")) this.chatPage = true;
      else if (to.path.includes("goto-app")) this.chatPage = true;
      else this.chatPage = false;
      this.routerKey = Math.random().toString(36).substring(2, 13);
    },
    //////////////////CHAT-END////////////////////
  },
};
</script>

