import axios from 'axios';
import router from '../router';

  const state = {
    message: '',
    token: "",
    authLoading: false,
    userEmail: "",
    expDate: 0,
    role: 0,
    confirmed: true,
    userId: '',
    stage: '',
    username: '',
    phone: '',
  };



  const mutations = {
    signin(state, status) {
      if(status === 200) {
          console.log('signed in successfully');
          state.message = 'signed in successfully';
      }
    },
    signup(state, status) {
        if(status === 200) {
            console.log('signed up successfully');
            state.message = 'signed up successfully';
            state.itemsStore.showModal = false;
        }
      },
    holdToken(state, token) {
      state.token = token;
    }
  };



  const actions = {
    signin({dispatch, commit, rootState}, body) {
      state.authLoading = true;
      axios.post('/signin', body).then(res => {
        state.authLoading = false;
        
        if(res.data.err) return alert(res.data.err);
          ////////// SET DATA //////////////////////
          dispatch("setUserData", res.data, body.pageToGo);
      })
    },

    signup({commit, rootState, dispatch}, body) {
        state.authLoading = true;
        axios.post('/signup', body).then( res => {
            state.authLoading = false;
            if(res.data.err) return alert(res.data.err);
            dispatch("setUserData", res.data, body.pageToGo);
        })
    },
    update({commit, rootState, dispatch}, body) {
      
    },
    closeMessage({commit}) {
        state.message = null;
    },
    async autoSignin({dispatch, commit, rootState}) {
            state.token = localStorage.getItem('token');
            state.userEmail = localStorage.getItem('userEmail');
            state.role = localStorage.getItem('role');
            state.stage = localStorage.getItem('stage');
            state.userId = localStorage.getItem('userId');
            state.username = localStorage.getItem('username');
            state.phone = localStorage.getItem('phone');
            state.confirmed = localStorage.getItem('confirmed');
            // if(state.confirmed == 1) 
            axios.defaults.headers.common['authorization'] = localStorage.getItem('token');
    },
    logout({commit, dispatch, rootState}) {
        dispatch("deleteData");
        router.replace('/register/home/signin');
    },
    setUserData({dispatch}, data, pageToGo) {
      state.userEmail = data.email;
          state.role = data.role;
          state.userId = data.userId;
          state.stage = data.stage;
          state.username = data.username;
          state.phone = data.phone;
          if(data.token) state.token = data.token;
          state.confirmed = data.confirmed;
          localStorage.setItem('expDate', expDate);
          localStorage.setItem('userEmail', data.email);
          localStorage.setItem('role', data.role);
          localStorage.setItem('userId', data.userId);
          localStorage.setItem('stage', data.stage);
          localStorage.setItem('username', data.username);
          localStorage.setItem('phone', data.phone);
          if(data.token) localStorage.setItem('token', data.token);
          localStorage.setItem('confirmed', data.confirmed);
          const expDate = new Date(new Date().getTime()+6000000);
          state.expDate = expDate;
            // if(data.confirmed===1) {
              state.message = 'signed in successfully';
              if(state.role==1) router.replace('/dashboard');
                else if(pageToGo==='exams') router.replace('/exam');
                else if(pageToGo==='fullCourse') router.replace('/fullCourse');
                else  router.replace('/');
                dispatch("getMsgs");
            // } else if(data.confirmed===0) {
            //   router.replace('/');
            //   state.message = '   ';
            // }
            dispatch('autoSignin');
    },
    writemessage({}, text) {
      state.message = text;
    },
    deleteData() {
      state.token = null;
      state.expDate = 0;
      state.userEmail = null;
      state.userId = null;
      state.role = null;
      state.stage = null;
      state.username = null;
      state.phone = null;
      state.userEmail = null;
      state.confirmed = false;
      localStorage.removeItem('token');
      localStorage.removeItem('expDate');
      localStorage.removeItem('userEmail');
      localStorage.removeItem('userId');
      localStorage.removeItem('role');
      localStorage.removeItem('stage');
      localStorage.removeItem('username');
      localStorage.removeItem('phone');
      localStorage.removeItem('confirmed');
    },
    repairLocalStorage({dispatch}) {
      if(!localStorage.getItem("token")) {
        dispatch("deleteData");
      }
    },
    async isConfirmed({commit, rootState}) {
      if(!state.userEmail) return;
      axios.get('/isConfirmed/'+ state.userEmail).then(res => {
        state.confirmed = res.data.confirmed;
        state.stage = res.data.stage;
        localStorage.setItem('confirmed', res.data.confirmed);
        localStorage.setItem('stage', res.data.stage);
      })
    },

  };



  const getters = {
    message(state) {
      return state.message;
    },
    authLoading(state) {
        return state.authLoading;
      },
    userEmail(state) {
        return state.userEmail;
    },
    token(state) {
        return state.token;
    },
    role(state) {
        return state.role;
    },
    userId(state) {
        return state.userId;
    },
    username(state) {
        return state.username;
    },
    phone(state) {
        return state.phone;
    },
    stage(state) {
        return state.stage;
    },
    confirmed(state) {
        return state.confirmed;
    }
   }



  export default {
    state,
    mutations,
    actions,
    getters
  }
