import CKEditor from "ckeditor4-vue"
Vue.use(CKEditor);
//////////////////////////////////////////////
//////////////////////////////////////////////

import VueZoomer from 'vue-zoomer'
Vue.use(VueZoomer);

//////////////////////////////////////////////
//////////////////////////////////////////////
let url1 = "";
let socketIo;
    if(Capacitor.isNative ) {
        url1 = process.env.VUE_APP_URL;
    }
    else url1 =  process.env.NODE_ENV == "production"? window.location.origin: "http://localhost:5000";
  
  socketIo = io(url1);
  //////////////////CHAT////////////////////
  axios.defaults.baseURL = url1;
  
  Vue.component("loading", Loading);
  Vue.component("responsive-table", ResponsiveTable);
  Vue.component("server-responsive-table", ServerResponsiveTable);
  Vue.component("app-table", Table);
  Vue.component("progress-bar", Progress);
  Vue.component("app-filter", Filter);
  Vue.component("app-modal", Modal);
  Vue.component("app-iframe", Iframe);
  Vue.component("display-question", DisplayQuestion);
  
  
  Vue.filter("stage", val => {
    let stage = '';
    switch(val) {
      case "1": stage = '  ';
      break;
      case "2": stage = '  ';
      break;
      case "3": stage = '  ';
      break;
      case "4": stage = '  ';
      break;
      case "5": stage = '  ';
      break;
      case "6": stage = '  ';
      break;
      default: stage = val;

    }
    return stage;
  });

  Vue.filter("month", val => {
    const months = ["January","February","March","April","May","June","July","Augast","Septemper","October","November","December"];
    return months[+val];
  })
  
  Vue.filter("number", val => {
    if(store.getters.adminData.language == 'english') return val;
    if(!val || val == "") return "";
    const Enum = val.toString().split("");
    const e = ['', '', '', '', '', '', '', '', '', ''];
    const Anum = Enum.map(n => {
      return e[+n];
    })
    return Anum.join("");
  });
  
  Vue.filter("section", (val, stage) => {
    const section = store.getters.sections.find((sec) => sec.number == val && sec.stage == stage);
    if(section) {
      return section.name;
    } else return val;
  });
  
  Vue.filter("student", (val, prop) => {
    const student = store.getters.students.find((user) => user._id == val);
    if(student) {
      return student[prop];
    } else return val;
  });
  
Vue.filter("truncate", val => {
  if(val.length < 20) return val;
  const chuncks = val.match(/.{1,20}/g);
  return chuncks.join(" ");
});
  
Vue.filter("translate", val => {
  const words = store.getters.adminData.language == 'english'? trans_en: trans_ar;
  console.log(words);
  if(words[val]) return words[val];
  else return val;
});



  Vue.use(Vuelidate);
  Vue.use(VueMoment);

  axios.defaults.headers.common['authorization'] = localStorage.getItem('token');
  Vue.config.productionTip = false
  
  new Vue({
    created() {
      AOS.init();
    },
    store,
    router,
    vuetify,
    render: h => h(App)
  }).$mount('#app')
  

////////////////////////////
export const getAdminData = () => store.getters.adminData;
//////////////////CHAT////////////////////
export const socket = () => socketIo;
//////////////////CHAT////////////////////
export const url = () => url1;
export const eventBus = new Vue();


