<template>
  <div
    v-if="src"
    :class="{ ltr: lang == '2', rtl: lang != '2' }"
    :dir="lang == '2' ? 'ltr' : 'rtl'"
    class=""
  >
    <div v-if="index" class="" style="margin-bottom: 3rem">
      <v-avatar color="teal" size="48">
        <span class="white--text text-h5">{{ index }}</span>
      </v-avatar>
    </div>
    <div class="ques-prev flex-grow-1">
      <div v-if="type == 'text'">
        <h4>{{ src }}</h4>
      </div>
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <!----------------------------------------------------->

      <div v-if="type == 'image' || type == 'sketch'" class="img-preview bg-light">
        <!-- <cld-image v-if="public_id" :publicId="public_id"> -->
        <!-- <cld-transformation angle="-45" /> -->
        <!-- <cld-transformation crop="pad" /> -->
        <!-- </cld-image> -->
        <v-zoomer style="width: 100%; min-height: 200px" class="center">
          <!---IMAGE--->
          <img
            class="img-fluid"
            @click="onImageError"
            @error="onImageError"
            :src="src.replace('http://', 'https://')"
            loading="lazy"
            style="object-fit: contain; width: 100%; height: 100%"
          />
        </v-zoomer>
      </div>
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <!----------------------------------------------------->

      <div v-if="type == 'video'" class="">
        <iframe
          :src="src"
          style="amx-width: 600px"
          height="300px"
          width="100%"
          frameborder="0"
        ></iframe>
      </div>
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <!----------------------------------------------------->

      <div v-if="type == 'audio'">
        <audio controls>
          <source :src="src" />
        </audio>
      </div>
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <div v-if="type == 'html'">
        <div v-html="src"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["src", "type", "lang", "index"],
  created() {
    // const arr = this.src.split(".");
    // const ext = arr[arr.length - 1];
    // const url = this.src.split(ext)[0];
    // this.public_id = url + "webp";
  },
  data() {
    return {
      public_id: null,
    };
  },
  methods: {
    onImageError(e) {
      setTimeout(function () {
        e.target.src = e.target.src;
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped>
.ques-prev {
  display: inline;
  vertical-align: center;
}
.img-preview {
  //   width: 500px;
  //   height: 500px;
  width: 100%;
}

._index {
  display: inline;
}
</style>
