import axios from 'axios';
import { eventBus } from '../main';

  const state = {
    pageData: {
      currentPage: 0,
      itemsPerPage: 2
    },
    order: '',
    showConfirmModal: false,
    wishloading: false,
    adminData: {
      instructions: "",
      siteName: "",
      name: "",
      phone: "",
      address: "",
      career: "",
      cashNumber: "",
      payMethod: "",
      image1: "",
      ad: "",
    },
    fullDegree: 60,
    order_modal: false,
    sections: [],
    students: [],
    exams: [],
    ready: false,
    stages: [
      {text: "  ", value: 1},
      {text: "  ", value: 2},
      {text: "  ", value: 3},
      {text: "  ", value: 4},
      {text: "  ", value: 5},
      {text: "  ", value: 6},
    ],
    showHeader: true,
    routerKey: 1,
    msgNumber: 0,
    code: null,
    langAlign: "",
    expVideos: {
      addExam: "",
      siteData: "",
      live: "",
      units: "",
      uplaodVideos: "",
      uploadMultiVideos: "",
      users: "",
      admin: "",
      students: "",
    }
  };



  const mutations = {
    putAdminData(state, data) {
      if(data) {
        state.adminData = data;
      }
    },
  };



  const actions = {
    getExamsFullDegree({rootState, commit}, data) {
      axios.get('/getStageStudentsOrder/'+data.stage+'/'+data.type).then(res => {
        state.fullDegree = res.data.fullDegree;
      })
    },
    async getSections({commit}) {  
      const res = await axios.get('/getSections');
      state.sections = res.data.sections;
    },
    async getUsers({commit}) {
      let method;
        method = axios.get('/getUsers');
      const res = await method;
      state.students = res.data.users;
    },
    async getExams({commit}) {
      const res = await axios.get('/getExamsByStage/' + localStorage.getItem("stage"));
      state.exams = res.data.exams;

    },
    getMsgs({commit, rootState}) {
      let res = {data: {messages: 0}};
      let api;
      if(rootState.usersStore.role == 0) api = "getUnseenMessagesNumber";
      else if(rootState.usersStore.role == 1) api = 'getUnseenUsersMessagesNumber';
        axios.get(`/${api}/` + rootState.usersStore.userId).then(re => {
          state.msgNumber = +res.data.messages;
        });
    },

    generateRouterKey() {
      state.routerKey = Math.random().toString(36).substring(2, 13);
    },
    changeMsgNumber({}, number) {
      console.log(number);
      state.msgNumber = +number;
    },
     setCode(_, code) {
      state.code = code;
     },
     toggleHeader(_, val) {
      state.showHeader = val;
     },

     setLangAlign() {
      const lang = state.adminData.language || "arabic";
      if (lang == "arabic") state.langAlign = "text-align: left; direction: ltr;";
      else state.langAlign = "text-align: right; direction: rtl;";
     },

    async initApp({commit, rootState, dispatch}) {
        // await dispatch("getUsers");
        dispatch("getSections");
        dispatch("getLessons");
        dispatch("isConfirmed");
        axios.get("/getAdminData").then(res => {
          commit("putAdminData", res.data.adminData);
          state.ready = true;
          // dispatch("adjust");
          
        });
        // await dispatch("getExams");
        await dispatch("repairLocalStorage");
        await dispatch("autoSignin");
        dispatch("setLangAlign");
        // dispatch("getMsgs");
    }
  };



  const getters = {
    adminData() {
      return state.adminData; 
    },
    fullDegree() {
      return state.fullDegree;
    },
    order_modal() {
      return state.order_modal;
    },
    sections() {
      return state.sections;
    },
    students() {
      return state.students;
    },
    exams() {
      return state.exams;
    },
    ready() {
      return state.ready;
    },
    stages() {
      if(state.adminData.multiVideos) {
        return state.stages.slice(3);
      }
      return state.stages;
    },
    routerKey() {
      return state.routerKey;
    },
    msgNumber() {
      return +state.msgNumber;
    },
    code() {
      return state.code;
    },
    langAlign() {
      return state.langAlign;
    },
    expVideos() {
      return state.expVideos;
    },
    showHeader() {
      return state.showHeader;
    },
  }



  export default {
    state,
    mutations,
    actions,
    getters
  }
