const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

function getItem(item) {
  return localStorage.getItem(item);
}

function onlyMobileGuard(to, from, next) {
  
console.log(store.getters.adminData.multiVideos)
  if(!store.getters.adminData.preventBrowserForStudents) return next();
  if(!Capacitor.isNative && localStorage.getItem('token') && localStorage.getItem('role') == 0) {
    return router.replace("/goto-app");
  }
  else return next();
}

export default router





